.input  {
  display: flex;
  align-items: center;
  min-height: 50px;
  padding: 0 20px;

  border-radius: 6px;
  font-weight: 400;
  font-size: 18px;
  color: $color-gray-dark;
  border: 1px solid $color-gray-light;
  transition: border $transition-time;

  &:focus, &.focus {
    border: 1px solid $color-orange;
  }
}

.input--type-text {
}

.input--type-text-checkbox {
  padding: 0;
  display: flex;
  align-items: center;

  &__icon {
    flex: 0 0 auto;
    display: inline-block;
    width: 15px;
    height: 15px;
    cursor: pointer;
    background: url("../img/input/checkbox-unchecked.svg") center/contain no-repeat;
  }
  &__input:checked+.input--type-text-checkbox__icon {
    background: url("../img/input/checkbox-checked.svg") center/contain no-repeat;
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.3em;
    color: $color-gray-dark;
    margin-left: .7em;
  }

  &__input {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }
}

.input--type-date {
  overflow: hidden;
  position: relative;
  padding: 0 10px 0 20px;


  &__input, &__placeholder {
    font-weight: 600;
    font-size: 18px;
    color: rgba($color-gray-dark, .8);
  }

  &__input {
    width: 100%;
    height: 50px;

    &::-webkit-calendar-picker-indicator {
      cursor: pointer;
      opacity: 1;
      display: block;
      background: url("../img/input/date.svg") center/contain no-repeat;

      width: 30px;
      height: 30px;
      border-width: thin;
    }
  }
  &__placeholder {
    display: flex;
    align-items: center;
    padding: 0 10px 0 20px;
    position: absolute;
    top: 0;
    left: 0;
    width: calc(100% - 55px);
    height: 100%;
    background-color: $color-white;
    opacity: 1;
    pointer-events: none;
    transition: opacity $transition-time;
  }
  &.active {
    .input--type-date__placeholder {
      opacity: 0;
    }
  }
}

.input--type-checkbox {
  display: flex;
  align-items: center;
  padding: 0 10px 0 20px;

  &__icon {
    flex: 0 0 auto;
    display: inline-block;
    width: 30px;
    height: 30px;
    cursor: pointer;
    background: url("../img/input/big-checkbox-unchecked.svg") center/contain no-repeat;
  }
  &__input:checked+.input--type-checkbox__icon {
    background: url("../img/input/big-checkbox-checked.svg") center/contain no-repeat;
  }

  &__text {
    font-weight: 600;
    font-size: 18px;
    line-height: 1.3em;
    color: rgba($color-gray-dark, .8);
    margin-right: auto;
  }

  &__input {
    position: absolute;
    z-index: -1;
    opacity: 0;
    width: 1px;
    height: 1px;
  }
}