::-webkit-scrollbar {
  width: 14px;
  height: 10px;
  background: $color-white;
}
::-webkit-scrollbar-thumb {
  border: 3px solid $color-white;
  border-radius: 100px;
  background: $color-orange;
}

::selection {
  background: $color-orange;
  color: $color-white;
}
::-moz-selection {
  background: $color-orange;
  color: $color-white;
}