.container {
  width: $container-width-xs;
  margin-right: auto;
  margin-left: auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -$gap-xs;
  margin-right: -$gap-xs;

  &--jc {
    &-start {
      justify-content: center;
    }

    &-center {
      justify-content: center;
    }

    &-end {
      justify-content: end;
    }

    &-sb {
      justify-content: space-between;
    }
  }

  &--ai {
    &-start {
      align-items: flex-start;
    }

    &-center {
      align-items: center;
    }

    &-end {
      align-items: flex-end;
    }
  }
}

[class^='col-'] {
  flex: 0 0 auto;
  padding-left: $gap-xs;
  padding-right: $gap-xs;
}

.col-xs-1 {
  width: 8.3334%;
}
.col-xs-2 {
  width: 16.6667%;
}
.col-xs-3 {
  width: 25%;
}
.col-xs-4 {
  width: 33.3334%;
}
.col-xs-5 {
  width: 41.6667%;
}
.col-xs-6 {
  width: 50%;
}
.col-xs-7 {
  width: 58.3334%;
}
.col-xs-8 {
  width: 66.6667%;
}
.col-xs-9 {
  width: 75%;
}
.col-xs-10 {
  width: 83.3334%;
}
.col-xs-11 {
  width: 91.6667%;
}
.col-xs-12 {
  width: 100%;
}

