.check-list {
  list-style-type: none;
  counter-reset: num;

  li {
    position: relative;
    padding-left: 1.2em;
    line-height: 130%;
    color: $color-gray-dark;
    font-weight: 500;
    @include smoothAdaptation(font-size, 14px, 18px);

    &:not(:last-child) {
      margin-bottom: 1em;
    }
  }
}
.check-list {
  li {
    .check-icon {
      svg {
        position: absolute;
        top: 0.25em;
        left: 0;
        color: $color-orange;
        width: 0.8em;
        height: 0.8em;
      }
    }
  }
}