.filter {
  @include smoothAdaptation(margin-bottom, 30px, 70px);

  &__wrapper {
    display: flex;
    justify-content: center;
    position: relative;

    .filter__stars {
      order: 2;
    }

    .filter__filter {
      order: 1;
      margin-right: 20px;
    }
  }

  &__stars {
    .rating__star {
      width: 22px;
      height: 22px;
    }
  }

  &__stars .filter__item {

    @media (any-hover: hover){
      &:hover .filter__item-content {
        opacity: 1;
        pointer-events: auto;
      }
    }
    &._active .filter__item-content {
      opacity: 1;
      pointer-events: auto;
    }
  }

  .filter__filter {

    @media (any-hover: hover){
      &:hover .filter__filter-content {
        opacity: 1;
        pointer-events: auto;
      }
    }
    &._active .filter__filter-content {
      opacity: 1;
      pointer-events: auto;
    }
  }



  &__stars .filter__item-content, .filter__filter-content {
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    opacity: 0;
    pointer-events: none;
    transition: opacity $transition-time;
    z-index: 10;
  }

  &__stars .filter__item-wrapper, .filter__filter-wrapper {
    padding: 0 100vw;
    margin: 0 -100vw;
    background-color: $color-white;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1), 0px 4px 15px rgba(0, 0, 0, 0.12);
  }

  &__stars .filter__item-body, .filter__filter-body {
    width: 100%;
    padding: 30px 0 75px;
  }


  &__item {
    .color--gray-dark .rating__star {
      color: $color-gray-dark;
    }
    .color--gray-light .rating__star {
      color: $color-gray-light;
    }
  }

  &__item-button:not(.item-button--rating) {
    font-weight: 600;
    font-size: 20px;
    color: $color-gray-dark;
  }

  &__item-button.item-button--rating {
    display: flex;
    align-items: center;
    font-size: 18px;
    padding: 0 15px;
    color: $color-gray-dark;
    border: 2px solid $color-orange;
    border-radius: 6px;
    cursor: pointer;
    @include smoothAdaptation(min-height, 35px, 45px);
  }

  &__item-body {
    & > *:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__filter {
  }

  &__filter-button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 10px;
    border: 2px solid $color-orange;
    border-radius: 6px;
    @include smoothAdaptation(min-height, 35px, 45px);

    svg {
      color: $color-orange;
      width: 24px;
      height: 24px;
    }
  }

  &__filter-body {
    display: flex;
    flex-direction: column;

    .filter__item:nth-child(1) {
      order: 2;
      margin-bottom: 35px;
      border-bottom: 2px solid $color-gray-light;
      padding-bottom: 30px;
    }
    .filter__item:nth-child(2) {
      order: 3;
    }
    &.filter-search {
      order: 1;
      margin-bottom: 30px;
    }

    .filter-search {
      margin-bottom: 20px;
    }

    .filter__item-button {
      margin-bottom: 20px;
    }
  }
}
.filter-radio {
  display: flex;
  align-items: center;

  &__input {
  }

  &__icon {
    display: block;
    width: 16px;
    height: 16px;
    margin: 2px;
    border-radius: 50%;
    background-color: $color-gray-dark;
    outline: 2px solid $color-gray-dark;
    border: 8px solid $color-white;
    cursor: pointer;
    transition: border $transition-time;
  }

  &__input:checked+.filter-radio__icon {
    border: 3px solid $color-white;
  }

  &__info {
  }

  &__content {
    display: flex;
    align-items: center;
    margin: 0 10px;
    cursor: pointer;
  }
}
.active {
}
.filter-search {

  &__body {
    overflow: hidden;
    position: relative;
    border-radius: 6px;
    border: 2px solid $color-gray-light;
    @include smoothAdaptation(min-height, 35px, 45px);
  }

  &__input {
    width: 100%;
    min-height: 45px;
    padding-right: 60px;
    font-size: 18px;
    color: $color-gray-dark;
    @include smoothAdaptation(padding-left, 15px, 25px);
  }

  &__button {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 60px;
    padding: 10px 15px;
    color: $color-orange;
    background-color: $color-white;

    @media (any-hover: hover){
      transition: color $transition-time;
      &:hover {
        color: rgba($color-orange, .8);
      }
    }
  }
}

.item-select__close {
  position: absolute;
  bottom: 35px;
  right: 15px;
  width: 15px;
  height: 15px;
  color: $color-gray-dark;
}





@media all and (min-width: $screen-md) {
  .filter {

    &__wrapper {

      .filter__stars {
        order: 1;
        margin-right: 15px;
      }

      .filter__filter {
        order: 2;
        margin: 0;
      }
    }

    &__stars {
      .rating__star {
      }
    }

    &__stars .filter__item {

      @media (any-hover: hover) {
        &:hover .filter__item-content {
        }
      }

      &._active .filter__item-content {
      }
    }

    &__filter {

      @media (any-hover: hover) {
        .filter__item:hover .filter__item-body {
          opacity: 1;
          pointer-events: auto;
        }
      }

      .filter__item._active .filter__item-body {
        opacity: 1;
        pointer-events: auto;
      }

      .filter__item-body {
        padding: 30px 50px 50px;
        position: absolute;
        left: 0;
        right: 0;
        min-width: 300px;
        opacity: 0;
        pointer-events: none;
        background-color: $color-white;
        transition: opacity 0.3s;
        z-index: 10;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1), 0px 4px 15px rgba(0, 0, 0, 0.12);
        border-radius: 10px;
      }
    }


    &__stars .filter__item-content, .filter__filter-content {
      min-width: 380px;
      padding-top: 5px;
    }

    .filter__filter-content {
      opacity: 1;
      pointer-events: auto;
      position: static;
      min-width: 100%;
      padding: 0;
    }

    &__stars .filter__item-wrapper, .filter__filter-wrapper {
      padding: 0;
      margin: 0;
      border-radius: 10px;
    }

    .filter__filter-wrapper {
      background-color: transparent;
      box-shadow: none;
    }

    &__stars .filter__item-body, .filter__filter-body {
      padding: 30px 50px 50px;

      & > *:not(:last-child) {
        margin-right: 15px;
      }
    }

    .filter__filter-body {
      flex-direction: row;
      padding: 0;
    }


    &__item {
      position: relative;
      margin: 0;
      padding: 0;
      border: none;

      &.item-filter--type {
      }

      .color--gray-dark .rating__star {
      }

      .color--gray-light .rating__star {
      }
    }

    &__item-button:not(.item-button--rating) {
      margin: 0;
    }

    &__item-button.item-button--rating {
    }

    &__item-body {
      & > *:not(:last-child) {
      }
    }

    &__filter {
    }

    &__filter-button {
      display: none;

      svg {
      }
    }

    &__filter-body {

      .filter__item:nth-child(1) {
        margin-bottom: 0;
        padding: 0;
        border: none;
        order: 1;
      }

      .filter__item:nth-child(2) {
        order: 2;
      }

      .filter-search {
        margin-bottom: 0;
        order: 3;
      }

      .filter__item-button {
        display: flex;
        align-items: center;
        font-size: 18px;
        font-weight: 400;
        padding: 0 15px;
        color: $color-gray-dark;
        border: 2px solid $color-orange;
        border-radius: 6px;
        cursor: pointer;
        @include smoothAdaptation(min-height, 35px, 45px);
      }
    }
  }
  .filter-radio {

    &__input {
    }

    &__icon {
    }

    &__input:checked + .filter-radio__icon {
    }

    &__info {
    }

    &__content {
    }
  }
  .active {
  }
  .filter-search {

    &__body {
      display: flex;
      align-items: center;
      border: 2px solid $color-orange;
      @include smoothAdaptation(min-height, 35px, 45px);
    }

    &__input {
      min-height: 100%;
    }

    &__button {
    }
  }

  .item-select__close {
    display: none;
  }

}




