// Colors
$color-white: #FFFFFF;
$color-black: #000000;

$color-gray-light: #A6B6C7;
$color-gray-dark: #23313A;

$color-blue-accent: #004FC5;
$color-blue-default: #1F637A;
$color-orange: #F47920;

// Effects

// Other
$transition-time: 0.3s;

// Grid
$screen-sm: 768px;
$screen-md: 1024px;
$screen-lg: 1280px;
$screen-xl: 1920px;

$gap-xs: 5px;
$gap-sm: 8px;
$gap-md: 10px;
$gap-lg: 10px;
$gap-xl: 15px;

$container-width-xs: 90vw;
$container-width-sm: 92vw;
$container-width-md: 970px;
$container-width-lg: (80px + $gap-lg * 2) * 12;
$container-width-xl: (100px + $gap-xl * 2) * 12;

// Fluid type functions
@mixin fluid-type($properties, $min-vw, $max-vw, $min-value, $max-value) {
  @each $property in $properties {
    #{$property}: calc(
      #{$min-value} +
        #{strip-unit($max-value - $min-value)} *
        (100vw - #{$min-vw}) /
        #{strip-unit($max-vw - $min-vw)}
    );
  }
}
@function strip-unit($number) {
  @if type-of($number) == 'number' and not unitless($number) {
    @return $number / ($number * 0 + 1);
  }
  @return $number;
}

// Use:  @include fluid-type(margin-bottom, 320px, 1920px, 25px, 70px);

@mixin smoothAdaptation($property, $min-value, $max-value, $round: true) {
  @if $round == true {
    $step: ($max-value - $min-value) / 4;

    #{$property}: $min-value;

    @media all and (min-width: $screen-sm) {
      #{$property}: round($min-value + $step);
    }
    @media all and (min-width: $screen-md) {
      #{$property}: round($min-value + ($step * 2));
    }
    @media all and (min-width: $screen-lg) {
      #{$property}: round($min-value + ($step * 3));
    }
    @media all and (min-width: $screen-xl) {
      #{$property}: $max-value;
    }
  }
  @else if $round == false {
    $step: ($max-value - $min-value) / 4;

    #{$property}: $min-value;

    @media all and (min-width: $screen-sm) {
      #{$property}: $min-value + $step;
    }
    @media all and (min-width: $screen-md) {
      #{$property}: $min-value + ($step * 2);
    }
    @media all and (min-width: $screen-lg) {
      #{$property}: $min-value + ($step * 3);
    }
    @media all and (min-width: $screen-xl) {
      #{$property}: $max-value;
    }
  }
}

@mixin adaptation($property, $value-xs, $value-sm, $value-md, $value-lg, $value-xl) {

  #{$property}: $value-xs;

  @media all and (min-width: $screen-sm) {
    #{$property}: $value-sm;
  }
  @media all and (min-width: $screen-md) {
    #{$property}: $value-md;
  }
  @media all and (min-width: $screen-lg) {
    #{$property}: $value-lg;
  }
  @media all and (min-width: $screen-xl) {
    #{$property}: $value-xl;
  }
}