.button-link {
  display: flex;
  align-items: center;

  &__icon {
    width: 28px;
    height: 28px;
    margin-right: 10px;
  }
  &__text {
    font-weight: 600;
    font-size: 18px;
  }

  &--color {
    &-orange {
      .button-link__icon {
        color: $color-orange;
      }
      .button-link__text {
        color: $color-gray-dark;

        @media (any-hover: hover){
          transition: color $transition-time;
          &:hover {
            color: $color-orange;
          }
        }
      }
    }
    &-white {
      .button-link__icon {
        color: $color-white;
      }
      .button-link__text {
        color: $color-white;

        @media (any-hover: hover){
          transition: color $transition-time;
          &:hover {
            color: rgba($color-white, 0.5);
          }
        }
      }
    }
  }
}

.button {
  display: inline-block;
  text-align: center;
  line-height: 100%;
  white-space: nowrap;
  font-weight: 600;
  color: $color-white;
  border-radius: 6px;
  background-size: 200% auto;
  background-image: linear-gradient(to right, $color-orange 50%, #FEBB01 100%);
  @include smoothAdaptation(font-size, 16px, 18px);

  @media (any-hover: hover){
    transition: background-position $transition-time;
    &:hover {
      background-position: right center;
    }
  }

  &--size {
    &-small {
      @include smoothAdaptation(padding-top, 15px, 12px);
      @include smoothAdaptation(padding-bottom, 15px, 12px);
      @include smoothAdaptation(padding-left, 30px, 20px);
      @include smoothAdaptation(padding-right, 30px, 20px);
    }
    &-big {
      padding: 20px 40px;
    }
  }
}