.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  overflow-y: auto;
  overflow-x: hidden;
  transition: opacity $transition-time, visibility $transition-time;
  background-color: rgba($color-black, .6);
  z-index: 900;
}
.popup.open {
  opacity: 1;
  visibility: visible;
}
.popup.open .popup__content {
  opacity: 1;
}

.popup__wrapper {
  min-height: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  transition: visibility $transition-time;
}
.popup__content {
  position: relative;
  padding: 25px;
  opacity: 0;
  max-width: 100%;
  width: 100%;
  color: $color-white;
  background-color: $color-white;
  transition: opacity $transition-time;
}

.popup {

  &__wrapper {
  }

  &__content {
  }

  &__close {
    position: absolute;
    width: 15px;
    height: 15px;
    color: $color-gray-dark;
    background-color: transparent;
    @include smoothAdaptation(top, 30px, 20px);
    @include smoothAdaptation(right, 30px, 20px);
  }

  &__logo {
    object-position: center;
    object-fit: contain;
    margin-left: auto;
    margin-right: auto;
    @include smoothAdaptation(width, 35px, 55px);
    @include smoothAdaptation(height, 45px, 70px);
    @include smoothAdaptation(margin-bottom, 25px, 30px);
  }

  &__title {
    font-weight: 600;
    font-size: 18px;
    text-align: center;
    margin-bottom: 25px;
    font-family: "Montserrat", sans-serif;
  }

  &__form {
    display: flex;
    flex-direction: column;

    & > *:not(:last-child) {
      margin-bottom: 15px;
    }
  }

  &__input {
  }

  &__button {
  }
}

@media all and (min-width: 360px) {
  .popup__content {
    padding: 40px;
  }
}
@media all and (min-width: 500px) {
  .popup__wrapper {
    align-items: center;
    padding: 30px 12px;
  }
  .popup__content {
    border-radius: 10px;
    max-width: 450px;
  }
}
.popup--thanks {
  .popup__wrapper {
  }

  .popup__content {
    min-height: 500px;
    text-align: center;
    color: $color-gray-dark;
    background: url("../img/popup/thanks-bg.jpg") center/cover no-repeat;
    position: relative;

    &:before {
      content: '';
      width: 200px;
      height: 95px;
      position: absolute;
      bottom: 10px;
      right: 20px;
      background: url("../img/popup/thanks-decor.png") center/cover no-repeat;
    }
  }

  .popup__close {
  }

  .popup__title {
    font-weight: 700;
    font-size: 30px;
    font-family: 'Spectral SC', serif;
    margin-top: 60px;
    margin-bottom: 10px;
  }

  .popup__subtitle {
    font-weight: 600;
    font-size: 20px;
  }
}

.popup--card {
  .popup__title {
    text-align: start;
    margin: 0;
    font-weight: 600;
    color: $color-gray-dark;
    @include smoothAdaptation(font-size, 22px, 26px);
  }
  .popup__subtitle {
    margin-bottom: 25px;
    font-weight: 600;
    color: $color-gray-dark;
    @include smoothAdaptation(font-size, 16px, 18px);
  }
}