.footer {
  background-color: $color-blue-default;
  color: $color-white;

  &__wrapper {
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      background: url("../img/footer/decor-1.png") center/contain no-repeat;
      transform: translate(0, -50%);
      @include smoothAdaptation(right, 0px, 300px);
      @include smoothAdaptation(width, 170px, 200px);
      @include smoothAdaptation(height, 80px, 90px);
    }
  }

  &__body {
    @include smoothAdaptation(padding-top, 60px, 25px);
    @include smoothAdaptation(padding-bottom, 35px, 30px);
  }

  &__logo {
  }

  &__logo-link {
  }

  &__logo-img {
    object-fit: contain;
    @include smoothAdaptation(height, 70px, 120px);
  }

  &__menu {
    margin: 35px 0;
  }

  &__menu-list {
  }

  &__menu-item {
  }

  &__menu-link {
    font-weight: 600;
    font-size: 16px;
    line-height: 175%;

    @media (any-hover: hover){
      position: relative;

      &:before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 0;
        height: 2px;
        background-color: $color-white;
        transition: width $transition-time;
        transform: translate(0, 100%);
      }

      &:hover {
        &:before {
          width: 100%;
        }
      }
    }
  }

  &__contacts {
  }

  &__contacts-phone {
    margin-bottom: 15px;

    .button-link__text {
      font-size: 24px;
      @include adaptation(font-size, 24px, 24px, 20px, 24px, 24px);
    }
  }

  &__contacts-address {
    font-weight: 600;
    font-size: 16px;
    line-height: 120%;
    color: $color-white;
  }

  &__footer {
    border-top: 2px solid rgb($color-white, .5);
    @include smoothAdaptation(padding-top, 25px, 20px);
    @include smoothAdaptation(padding-bottom, 60px, 20px);
  }

  &__footer-list {
    display: flex;
    align-items: stretch;
    flex-direction: column;
  }

  &__footer-item {
    &:not(:last-child) {
      margin-bottom: 20px;
      @include adaptation(margin-right, 0px, 20px, 45px, 70px, 100px);
    }
  }

  &__footer-link {
    color: rgba($color-white, .6);

    @media (any-hover: hover){
      transition: color $transition-time;
      &:hover {
        color: rgb($color-white, 1);
      }
    }
  }
}

@media all and (min-width: $screen-sm) {
  .footer {
    &__footer-list {
      flex-direction: row;
      align-items: center;
    }
    &__footer-item {
      &:not(:last-child) {
        margin-bottom: 0;
      }
    }
  }
}


