.pagination {
  display: flex;
  justify-content: center;

  &__body {
    display: flex;
  }

  &__arrow, &__number {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    font-size: 14px;
    font-weight: 700;
    border: 1px solid $color-gray-light;
    border-radius: 4px;
    margin-right: 8px;

    @media (any-hover: hover) {
      transition: color $transition-time, background-color $transition-time;

      svg {
        transition: color $transition-time;
      }
      &:hover {
        color: $color-white;
        background-color: $color-gray-light;

        svg {
          color: $color-white;
        }
      }
    }
  }

  &__arrow {
    &.disabled {
      background: #A6B6C7;
      opacity: 0.5;
      svg {
        color: $color-white;
      }
    }

    svg {
      width: 24px;
      height: 24px;
      color: $color-gray-light;
    }
    &._prev {
      svg {
      }
    }
    &._next {
      margin-right: 0;
      svg {
        transform: rotate(180deg);
      }
    }
  }

  &__list {
    display: flex;
  }

  &__item {
    &.is-active {
      .pagination__number {
        color: $color-orange;
        border-color: $color-orange;
        background-color: transparent;
      }
    }
  }

  &__number {
    color: $color-gray-dark;

    .is-collapsed {
    }
  }
}