.message-screen {
  background: url("../img/message-screen/bg.jpg") center/cover no-repeat;
  @include smoothAdaptation(padding-top, 80px, 130px);
  @include smoothAdaptation(padding-bottom, 80px, 130px);

  &__title {
    text-align: center;
    @include smoothAdaptation(margin-bottom, 40px, 60px);
  }

  &__body {
  }

  &__text {
    font-weight: 500;
    line-height: 135%;
    margin-bottom: 30px;
    color: $color-gray-dark;
    @include smoothAdaptation(padding-right, 20px, 80px);
    @include smoothAdaptation(font-size, 16px, 24px);
  }

  &__list {
  }

  &__list-item {

    .check-icon {
    }
  }
}

@media all and (min-width: $screen-sm) {
  .message-screen {
    &__text {
      margin-bottom: 0;
    }
  }
}