.card {
  &__body {
    background-color: $color-white;
    box-shadow: 2px 0px 8px rgba($color-black, 0.12), 0px 2px 25px rgba($color-black, 0.1);
    border-radius: 10px;
    overflow: hidden;
    outline: 2px solid transparent;
    transition: outline-color $transition-time;

    @media (any-hover: hover){
      &:hover {
        outline-color: $color-orange;

        .card__media-control {
          opacity: 1;
        }
      }
    }
  }
  &__media {

    &-body {
      position: relative;
    }

    &-wrapper {
    }

    &-slide {
    }

    &-slide-item {
      position: relative;
      padding: 0 0 60% 0;

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &-control {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      transition: opacity $transition-time;

      @media (any-hover: hover){
        opacity: 0;
      }

      &-navigation {
        width: 28px;
        height: 28px;
        color: $color-orange;
        border-radius: 50%;
        border: 3px solid $color-orange;
        background-color: $color-white;
        cursor: pointer;

        @media (any-hover: hover){
          transition: opacity $transition-time;

          &:hover {
            opacity: .7;
          }
        }

        &.navigation--prev {
          position: absolute;
          top: 50%;
          left: 20px;
          transform: translate(0, -50%) rotate(180deg);
        }

        &.navigation--next {
          position: absolute;
          top: 50%;
          right: 20px;
          transform: translate(0, -50%);
        }

        &.swiper-button-lock {
          display: none;
        }
      }

      &-pagination {
        position: absolute;
        bottom: 20px;
        left: 50%;
        font-weight: 600;
        font-size: 20px;
        transform: translate(-50%, 0);
        color: $color-white;
      }
    }
  }
  &__texts {
    padding-top: 20px;
    @include smoothAdaptation(padding-bottom, 20px, 25px);
    @include smoothAdaptation(padding-left, 20px, 25px);
    @include smoothAdaptation(padding-right, 20px, 25px);
  }
  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
  }
  &__location {
    font-size: 18px;
    color: $color-gray-dark;
  }
  &__rating {}
  &__titles {
    font-weight: 600;
    color: $color-gray-dark;
    margin-bottom: 20px;
  }
  &__name {
    @include smoothAdaptation(font-size, 22px, 26px);
  }
  &__type {
    @include smoothAdaptation(font-size, 16px, 18px);
  }
  &__distance {
    display: flex;
    align-items: flex-end;
    font-weight: 600;
    color: $color-blue-accent;
    line-height: .8em;
    margin-bottom: 15px;

    &-icon {
      margin-right: 10px;
      @include smoothAdaptation(width, 24px, 28px);
      @include smoothAdaptation(height, 24px, 28px);
    }

    &-text {
    }
  }
  &__icons {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 10px;

    &-icon {
      color: #91999D;
      @include smoothAdaptation(width, 24px, 28px);
      @include smoothAdaptation(height, 24px, 28px);

      &:not(:last-child) {
        margin-right: 15px;
      }
    }
  }
  &__button-block {
    text-align: right;
  }
  &__button {
    &:disabled {
      cursor: not-allowed;
      background: $color-gray-light;
    }
  }
}


.card {
  &__body {}
  &__media {
    img {
    }
  }
  &__texts {}
  &__head {}
  &__location {}
  &__rating {}
  &__titles {}
  &__name {}
  &__type {}
  &__distance {
    &-icon {
    }

    &-text {
    }
  }
  &__icons {
    &-icon {
    }
  }
  &__button-block {}
  &__button {}
}