.first-screen {
  background: url("../img/first-screen/bg.jpg") center/cover no-repeat;
  @include smoothAdaptation(padding-top, 60px, 80px);
  @include smoothAdaptation(margin-bottom, 150px, 230px);

  &__text {
    &-body {
      text-align: center;
      @include smoothAdaptation(padding-top, 70px, 150px);
      @include smoothAdaptation(padding-bottom, 0px, 135px);
    }

    &-title {
      font-weight: 700;
      line-height: 105%;
      font-family: 'Spectral SC', serif;
      color: $color-white;
      text-shadow: 3px 2px 0px $color-orange;
      @include smoothAdaptation(font-size, 35px, 80px);
      @include smoothAdaptation(margin-bottom, 12px, 0px);
    }

    &-subtitle {
      font-weight: 600;
      color: $color-white;
      font-family: 'Montserrat', sans-serif;
      @include smoothAdaptation(font-size, 16px, 40px);
      @include smoothAdaptation(margin-bottom, 35px, 45px);
    }

    &-button {
    }
  }

  &__media {
    &-body {
      transform: translate(0, 20%);
    }

    &-slider {
      &-body {
        margin: 0 -100vw;
        padding: 0 100vw;
      }

      &-wrapper {
      }

      &-slide {
        &-item {
          position: relative;
          padding: 0 0 60% 0;
          border-radius: 10px;
          overflow: hidden;

          &:last-child {
            @include smoothAdaptation(margin-top, 15px, 20px);
          }
        }

        img {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
  }
}

@media all and (min-width: $screen-sm) {
  .first-screen {
    &__text {
      &-title {
        text-shadow: 5px 3px 0px $color-orange;
      }
    }
  }
}


