.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  display: flex;
  align-items: center;
  background-color: transparent;
  padding: 15px 0;
  z-index: 800;
  @include smoothAdaptation(min-height, 60px, 80px);
  transition: background-color $transition-time;

  &.scrolling {
    background-color: $color-white;
  }

  &__body {
  }

  &__logo {
    &-link {
      display: flex;
      align-items: center;
    }

    &-icon {
      @include smoothAdaptation(height, 30px, 50px);
    }
    &-text {
      margin-left: 10px;
      @include smoothAdaptation(height, 30px, 35px);
      display: none;
    }
  }

  &__buttons {
    display: flex;
    justify-content: flex-end;
    align-items: center;

    &-phone, &-account {
      .button-link__icon {
        margin: 0;
      }
      .button-link__text {
        display: none;
      }
    }

    &-phone {
      margin-right: 20px;
     }

    &-account {
    }
  }
}

@media all and (min-width: 350px) {
  .header {
    &__body {
    }

    &__logo {
      &-icon {
      }
      &-text {
        display: block;
      }
    }

    &__buttons {

      &-phone {
      }

      &-account {
      }
    }
  }
}

@media all and (min-width: $screen-sm) {
  .header {
    &__body {
    }

    &__logo {
      &-link {
      }

      &-img {
      }
    }

    &__buttons {

      &-phone, &-account {
        .button-link__icon {
          margin: 10px;
        }
        .button-link__text {
          display: block;
        }
      }

      &-phone {
      }

      &-account {
      }
    }
  }
}

